@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');

body {
    font-family: 'Roboto', sans-serif;
}

$color-text: #121212;
$button-text: #FFFFFF;
$button-background: #E4C1AE;
$jumbotron-background: #F0F0F0;
$font-large: 42px/49px Roboto;
$font-normal: 16px/26px Roboto;

@mixin additional-text-settings {
    letter-spacing: 0px;
    opacity: 1;
}

h1 {
    text-align: left;
    font: normal normal bold $font-large;
    color: $color-text;
    @include additional-text-settings();
    margin-top: 86px;
}

p {
    text-align: left;
    font: normal normal 300 $font-normal;
    color: $color-text;
    @include additional-text-settings();
    margin-top: 43px;
    margin-bottom: 0px;
}

.btn {
    text-align: center;
    font: normal normal bold $font-normal;
    color: $button-text;
    @include additional-text-settings();
    background: $button-background 0% 0% no-repeat padding-box;
    width: 135px;
    height: 47px;
    margin-top: 35px;
    margin-bottom: 8px;
    border-radius: 50px;
    border: 0px;
}

.jumbotron {
    border-radius: 0;
    background-color: $jumbotron-background;
    padding: (100 / 1280) * 100vw (70 / 1280) * 100vw;
}

@media (min-width: 1280px) {
    .jumbotron {
        max-width: 1280px;
        padding: 100px 70px;
    }
}